* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

#search-box:hover,
#search-box:focus,
#search-box:focus-visible {
  border: 2px solid #0000CD;
  outline: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

li {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

h1 {
  margin: 0;
  font-size: 25px;
  font-weight: 700;
}

h2 {
  margin: 0;
  font-size: 12px;
  font-weight: 400;
}

img {
  margin-right: 15px;
}

p {
  margin: 0;
  font-size: 12px;
  font-weight: 400;
}